import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import { SynopsisContainer } from './style';

const propTypes = {
  synopsis: PropTypes.string.isRequired,
};

const defaultProps = {};

export default function SynopsisSection({ synopsis, poster }) {
  return (
    <SynopsisContainer>
      <Row>
        <Col
          xs={{ size: 12, order: 2 }}
          sm={{ size: 12, order: 2 }}
          md={{ size: 9, order: 1 }}
          className="synopsis"
        >
          {synopsis}
        </Col>
        <Col
          xs={{ size: 12, order: 1 }}
          sm={{ size: 12, order: 1 }}
          md={{ size: 3, order: 2 }}
          className="poster"
        >
          <img src="data:image/svg+xml;charset=UTF-8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20version%3D%221.1%22%20baseProfile%3D%22full%22%20width%3D%22undefined%22%20height%3D%22undefined%22%3E%20%3Crect%20width%3D%22100%25%22%20height%3D%22100%25%22%20fill%3D%22grey%22%2F%3E%20%20%3Ctext%20x%3D%220%22%20y%3D%2220%22%20font-size%3D%2220%22%20text-anchor%3D%22start%22%20fill%3D%22white%22%3Eundefinedxundefined%3C%2Ftext%3E%20%3C%2Fsvg%3E" alt="" style={{ width: '100%' }} />
        </Col>
      </Row>
    </SynopsisContainer>
  );
}

SynopsisSection.propTypes = propTypes;
SynopsisSection.defaultProps = defaultProps;